<template>
  <div class="rules-container">
    <div class="page-header">
      <h1></h1>
      <div>
        <el-button type="primary" @click="$router.push('/servers')"></el-button>
        <el-button v-if="isServerOnline && !scriptExists" type="success" @click="deployScript"
          :loading="deploying"></el-button>
        <el-button v-if="isServerOnline && scriptExists" type="danger" @click="confirmClearRules"></el-button>
        <el-button v-if="!isServerOnline && server" type="warning" @click="tryConnectServer"
          :loading="connecting"></el-button>
      </div>
    </div>

    <div v-if="server" class="server-info">
      <h2>{{ server.name }} <el-tag :type="server.status === 'online' ? 'success' : 'danger'">{{ server.status ===
        'online' ? '' : '' }}</el-tag></h2>
      <p>{{ server.host }}:{{ server.port }} ({{ server.username }})</p>
    </div>

    <!--  -->
    <div v-if="!scriptCheckLoading && !scriptExists && isServerOnline" class="script-deploy-needed">
      <el-alert
        title=""
        type="warning"
        description="Nftato"
        show-icon
        :closable="false"
        style="margin-bottom: 15px;">
      </el-alert>
      
      <div class="deploy-container">
        <div class="deploy-intro">
          <i class="el-icon-warning"></i>
          <h3>Nftato</h3>
          <p>Nftatonftables</p>
          <p>""</p>
        </div>
        
        <el-button type="success" size="large" @click="deployScript" :loading="deploying">
          <i class="el-icon-upload"></i> 
        </el-button>
      </div>
    </div>
    
    <!--  -->
    <div v-if="deploying && deployLogs.length > 0" class="deploy-terminal">
      <div class="terminal-header">
        <span></span>
        <el-button v-if="deployComplete" size="mini" type="success" @click="deployLogs = []"></el-button>
      </div>
      <div class="terminal-body" ref="terminalBody">
        <div v-for="(log, index) in deployLogs" :key="index" 
             :class="{'log-line': true, 'error-line': log.type === 'error', 'success-line': log.type === 'success'}">
          <pre>{{ log.message }}</pre>
        </div>
        <div v-if="deploying && !deployComplete" class="terminal-cursor"></div>
      </div>
      <div class="terminal-footer" v-if="deployComplete">
        <el-button v-if="deploySuccess" type="success" @click="refreshAllData"></el-button>
        <el-button v-else type="danger" @click="retryDeploy"></el-button>
      </div>
    </div>

    <el-tabs v-model="activeTab" type="card" v-if="scriptExists || !isServerOnline">
      <el-tab-pane label="" name="inbound">
        <template v-if="!isServerOnline">
          <el-alert title="" type="warning" description="" show-icon :closable="false"
            style="margin-bottom: 15px;">
          </el-alert>

          <div class="server-offline">
            <i class="el-icon-connection"></i>
            <h3></h3>
            <p></p>
          </div>

          <div class="offline-actions">
            <el-button type="primary" @click="tryConnectServer" :loading="connecting"
              icon="el-icon-refresh"></el-button>
            <el-button @click="$router.push('/servers')" icon="el-icon-back"></el-button>
          </div>
        </template>

        <div v-else>
          <el-card>
            <div slot="header">
              <span>SSH</span>
              <el-button style="float: right; padding: 3px 0" type="text" @click="refreshSSHPort"></el-button>
            </div>

            <pre v-if="sshPortStatus" class="output">{{ sshPortStatus }}</pre>
            <div v-else>...</div>
          </el-card>

          <el-card style="margin-top: 20px;">
            <div slot="header">
              <span></span>
              <el-button style="float: right; padding: 3px 0" type="text" @click="refreshInboundPorts"
                :loading="loadingPorts"></el-button>
            </div>

            <el-table v-loading="loadingPorts" :data="inboundPorts" style="width: 100%">
              <el-table-column prop="port" label="" width="180"></el-table-column>
              <el-table-column prop="protocol" label="" width="100"></el-table-column>
              <el-table-column label="">
                <template slot-scope="scope">
                  <el-tooltip v-if="isSshPort(scope.row.port)" content="SSH" placement="top">
                    <el-button type="danger" size="mini" disabled></el-button>
                  </el-tooltip>
                  <el-button v-else type="danger" size="mini" @click="disallowPort(scope.row.port)"
                    :loading="loadingPorts" :disabled="!isServerOnline"></el-button>
                </template>
              </el-table-column>
            </el-table>

            <el-divider></el-divider>

            <el-form :inline="true" @submit.native.prevent="allowPort">
              <el-form-item label="">
                <el-input v-model="portToAllow" placeholder=": 80,443" :disabled="!isServerOnline"></el-input>
              </el-form-item>
              <el-form-item>
                <el-button type="primary" @click="allowPort" :loading="loadingPorts"
                  :disabled="!isServerOnline"></el-button>
              </el-form-item>
            </el-form>
          </el-card>

          <el-card style="margin-top: 20px;">
            <div slot="header">
              <span>IP</span>
              <el-button style="float: right; padding: 3px 0" type="text" @click="refreshInboundIPs"
                :loading="loadingIPs"></el-button>
            </div>

            <el-table v-loading="loadingIPs" :data="inboundIPs" style="width: 100%">
              <el-table-column prop="ip" label="IP" width="180"></el-table-column>
              <el-table-column label="">
                <template slot-scope="scope">
                  <el-button type="danger" size="mini" @click="disallowIP(scope.row.ip || scope.row)"
                    :loading="loadingIPs" :disabled="!isServerOnline"></el-button>
                </template>
              </el-table-column>
            </el-table>

            <el-divider></el-divider>

            <el-form :inline="true" @submit.native.prevent="allowIP">
              <el-form-item label="IP">
                <el-input v-model="ipToAllow" placeholder=": 192.168.1.1" :disabled="!isServerOnline"></el-input>
              </el-form-item>
              <el-form-item>
                <el-button type="primary" @click="allowIP" :loading="loadingIPs"
                  :disabled="!isServerOnline"></el-button>
              </el-form-item>
            </el-form>
          </el-card>
        </div>
      </el-tab-pane>

      <el-tab-pane label="" name="outbound">
        <template v-if="!isServerOnline">
          <el-alert title="" type="warning" description="" show-icon :closable="false"
            style="margin-bottom: 15px;">
          </el-alert>

          <div class="server-offline">
            <i class="el-icon-connection"></i>
            <h3></h3>
            <p></p>
          </div>

          <div class="offline-actions">
            <el-button type="primary" @click="tryConnectServer" :loading="connecting"
              icon="el-icon-refresh"></el-button>
            <el-button @click="$router.push('/servers')" icon="el-icon-back"></el-button>
          </div>
        </template>

        <div v-else>
          <el-card>
            <div slot="header">
              <span></span>
              <el-button style="float: right; padding: 3px 0" type="text" @click="refreshBlockList"
                :loading="loadingBlockList"></el-button>
            </div>

            <pre v-if="blockList" class="output">{{ blockList }}</pre>
            <div v-else>...</div>
          </el-card>

          <el-card style="margin-top: 20px;">
            <div slot="header">
              <span></span>
            </div>
            <el-button-group>
              <el-button type="primary" @click="blockSPAM" :loading="loading"
                :disabled="!isServerOnline">SPAM</el-button>
            </el-button-group>

            <el-divider></el-divider>

            <el-form :inline="true" @submit.native.prevent="blockCustomPorts">
              <el-form-item label="">
                <el-input v-model="customPorts" placeholder=": 6881,6882-6889" :disabled="!isServerOnline"></el-input>
              </el-form-item>
              <el-form-item>
                <el-button type="warning" @click="blockCustomPorts" :loading="loading"
                  :disabled="!isServerOnline"></el-button>
              </el-form-item>
            </el-form>
          </el-card>

          <el-card style="margin-top: 20px;">
            <div slot="header">
              <span></span>
            </div>
            <el-button-group>
              <el-button type="success" @click="unblockSPAM" :loading="loading"
                :disabled="!isServerOnline">SPAM</el-button>
            </el-button-group>

            <el-divider></el-divider>

            <el-form :inline="true" @submit.native.prevent="unblockCustomPorts">
              <el-form-item label="">
                <el-input v-model="customUnblockPorts" placeholder=": 6881,6882-6889"
                  :disabled="!isServerOnline"></el-input>
              </el-form-item>
              <el-form-item>
                <el-button type="success" @click="unblockCustomPorts" :loading="loading"
                  :disabled="!isServerOnline"></el-button>
              </el-form-item>
            </el-form>
          </el-card>
        </div>
      </el-tab-pane>

      <el-tab-pane label="DDoS" name="ddos">
        <template v-if="!isServerOnline">
          <el-alert title="" type="warning" description="DDoS" show-icon
            :closable="false" style="margin-bottom: 15px;">
          </el-alert>

          <div class="server-offline">
            <i class="el-icon-connection"></i>
            <h3></h3>
            <p>DDoS</p>
          </div>

          <div class="offline-actions">
            <el-button type="primary" @click="tryConnectServer" :loading="connecting"
              icon="el-icon-refresh"></el-button>
            <el-button @click="$router.push('/servers')" icon="el-icon-back"></el-button>
          </div>
        </template>

        <div v-else>
          <el-card>
            <div slot="header">
              <span></span>
              <el-button style="float: right; padding: 3px 0" type="text" @click="refreshDefenseStatus"
                :loading="loadingDefenseStatus"></el-button>
            </div>

            <pre v-if="defenseStatus" class="output">{{ defenseStatus }}</pre>
            <div v-else>...</div>
          </el-card>

          <el-card style="margin-top: 20px;">
            <div slot="header">
              <span>DDoS</span>
            </div>
            <el-button-group>
              <el-button type="primary" @click="setupDdosProtectionAction" :loading="loading"
                :disabled="!isServerOnline">DDoS</el-button>
              <el-button type="primary" @click="showIpListsDialog" :loading="loading"
                :disabled="!isServerOnline">IP</el-button>
            </el-button-group>

            <el-divider></el-divider>

            <h4>DDoS</h4>
            <el-form label-width="140px" @submit.native.prevent="setupCustomPortProtectionAction">
              <el-form-item label="">
                <el-input v-model="customDdosPort" placeholder=": 8080" :disabled="!isServerOnline"
                  style="width: 200px"></el-input>
              </el-form-item>

              <el-form-item label="">
                <el-select v-model="customDdosProtoType" placeholder="" :disabled="!isServerOnline"
                  style="width: 200px">
                  <el-option label="TCP" :value="1"></el-option>
                  <el-option label="UDP" :value="2"></el-option>
                  <el-option label="TCP+UDP" :value="3"></el-option>
                </el-select>
              </el-form-item>

              <el-form-item label="IP">
                <el-input-number v-model="customDdosMaxConn" :min="100" :max="1000" :step="50"
                  :disabled="!isServerOnline"></el-input-number>
              </el-form-item>

              <el-form-item label="">
                <el-input-number v-model="customDdosMaxRateMin" :min="100" :max="1000" :step="50"
                  :disabled="!isServerOnline"></el-input-number>
              </el-form-item>

              <el-form-item label="">
                <el-input-number v-model="customDdosMaxRateSec" :min="50" :max="500" :step="25"
                  :disabled="!isServerOnline"></el-input-number>
              </el-form-item>

              <el-form-item label="IP">
                <el-input-number v-model="customDdosBanHours" :min="1" :max="72" :step="1"
                  :disabled="!isServerOnline"></el-input-number>
                <span class="form-item-tip"></span>
              </el-form-item>

              <el-form-item>
                <el-button type="primary" @click="setupCustomPortProtectionAction" :loading="loading"
                  :disabled="!isServerOnline"></el-button>
              </el-form-item>
            </el-form>
          </el-card>
        </div>
      </el-tab-pane>
    </el-tabs>
    
    <!--  -->
    <div v-if="scriptCheckLoading && isServerOnline" class="loading-container">
      <el-card>
        <div class="loading-content">
          <i class="el-icon-loading"></i>
          <p>...</p>
        </div>
      </el-card>
    </div>
  </div>
</template>

<script>
import { mapActions, mapGetters } from 'vuex';
import io from 'socket.io-client';

export default {
  name: 'RulesView',
  props: {
    serverId: {
      type: String,
      required: true
    }
  },
  data() {
    return {
      activeTab: 'inbound',
      loading: false,
      deploying: false,
      connecting: false,
      loadingPorts: false,
      loadingIPs: false,
      loadingSSHPort: false,
      loadingBlockList: false,
      loadingDefenseStatus: false,
      loadingDeployment: false,
      loadingRefreshAll: false,
      server: null,
      blockList: '',
      sshPortStatus: '',
      sshPort: null,
      inboundPorts: [],
      inboundIPs: [],
      commandOutput: '',
      customPorts: '',
      customKeyword: '',
      customUnblockPorts: '',
      portToAllow: '',
      ipToAllow: '',
      isInitialized: false,
      initStepActive: 0,
      initializationSteps: [
        { name: '', done: false },
        { name: '', done: false },
        { name: '', done: false },
        { name: '', done: false }
      ],
      debugging: false,
      debugInfo: '',
      statusCheckTimer: null,
      dataCache: {
        blockList: null,
        sshPortStatus: null,
        inboundPorts: null,
        inboundIPs: null
      },
      cacheTTL: {
        blockList: 60 * 1000, // 1
        sshPortStatus: 60 * 1000,
        inboundPorts: 60 * 1000,
        inboundIPs: 60 * 1000
      },
      cacheTimestamps: {
        blockList: 0,
        sshPortStatus: 0,
        inboundPorts: 0,
        inboundIPs: 0
      },
      dataLoaded: {
        blockList: false,
        sshPortStatus: false,
        inboundPorts: false,
        inboundIPs: false
      },
      serverCacheAvailable: false,
      serverCacheLastUpdate: null,
      defenseStatus: '',
      customDdosPort: '',
      customDdosProtoType: 1,
      customDdosMaxConn: 500,
      customDdosMaxRateMin: 500,
      customDdosMaxRateSec: 250,
      customDdosBanHours: 24,
      ipListsDialogVisible: false,
      ipListsActiveTab: 'addWhite',
      ipToManage: '',
      ipDuration: 0,
      ipManageResult: '',
      // 
      retryConfig: {
        maxRetries: 2,
        retryDelay: 1000
      },
      // 
      criticalPorts: [22, 80, 443, 3306, 6379, 8080, 8443, 27017, 5432],
      // 
      ipOperationDebounce: {
        timer: null,
        lastIp: '',
        lastAction: null,
        cooldown: false,
        timeout: 2000 // 2
      },
      
      // 
      scriptExists: false,
      scriptCheckLoading: true,
      deployLogs: [],
      socket: null,
      deployRoomId: null,
      deployComplete: false,
      deploySuccess: false,
    };
  },
  computed: {
    ...mapGetters('servers', ['getLoading']),
    hasValidServerId() {
      return !!this.serverId && this.serverId !== 'undefined';
    },
    isServerOnline() {
      return this.server && this.server.status === 'online';
    },
    // 
    serverStatusText() {
      if (!this.server) return '';
      switch (this.server.status) {
        case 'online': return '';
        case 'offline': return '';
        case 'connecting': return '';
        case 'disconnecting': return '';
        default: return '';
      }
    },
    // 
    isServerAvailable() {
      return this.server && ['online', 'connecting'].includes(this.server.status);
    },
    // 
    isServerTransitioning() {
      return this.server && ['connecting', 'disconnecting'].includes(this.server.status);
    }
  },
  beforeRouteEnter(to, from, next) {
    if (!to.params.serverId) {
      next(vm => {
        vm.$message.error('ID');
        vm.$router.push('/servers');
      });
    } else {
      next();
    }
  },
  created() {
    this.activeTab = 'inbound';

    if (this.hasValidServerId) {
      this.$nextTick(async () => {
        await this.initializeApplication();
      });

      this.startServerStatusCheck();
    } else {
      this.handleInvalidServerId();
    }
  },
  beforeDestroy() {
    this.stopServerStatusCheck();
    
    // WebSocket
    if (this.socket) {
      this.socket.disconnect();
      this.socket = null;
    }
  },
  methods: {
    ...mapActions('servers', [
      'getServer',
      'deployIptato',
      'connectServer',
      'testSSHConnection',
      'checkScriptExists',
      'resetConnectionStatus'
    ]),
    ...mapActions('rules', [
      'getBlockList',
      'blockSPAMAction',
      'blockCustomPortsAction',,
      'unblockSPAMAction',
      'unblockCustomPortsAction',
      'getInboundPorts',
      'getInboundIPs',
      'allowInboundPortsAction',
      'disallowInboundPortsAction',
      'allowInboundIPsAction',
      'disallowInboundIPsAction',
      'getSSHPort',
      'clearAllRulesAction',
      'getServerCache',
      'getCacheLastUpdate',
      'clearServerCache',
      'updateCacheItem',
      'setupDdosProtection',
      'setupCustomPortProtection',
      'manageIpLists',
      'getDefenseStatus'
    ]),
    async initializeApplication() {
      try {
        this.commandOutput = '...';
        await this.autoResetConnectionState();
        
        if (!this.hasValidServerId) {
          this.commandOutput = 'ID';
          this.$message.error('ID');
          return false;
        }

        this.resetInitSteps();
        this.isInitialized = false;
        this.initStepActive = 0;

        this.commandOutput = '...';
        this.loading = true;

        // 1: 
        const serverResponse = await this.getServer(this.serverId);
        if (!serverResponse || !serverResponse.success) {
          throw new Error(serverResponse?.error || '');
        }
        this.server = serverResponse.data;
        this.initializationSteps[0].done = true;
        this.initStepActive = 1;
        
        // 
        const cacheLoaded = await this.loadServerCache();
        if (cacheLoaded) {
          this.commandOutput += '\n';
        }

        // 2: 
        if (!this.server.status || this.server.status !== 'online') {
          this.commandOutput += '\n...';
          this.connecting = true;
          try {
            const connectResponse = await this.connectServer(this.serverId);
            this.connecting = false;
            
            if (!connectResponse || !connectResponse.success) {
              if (cacheLoaded) {
                this.$message.warning('');
                this.commandOutput += '\n';
                // 
                this.completeInitialization(cacheLoaded);
                return true;
              } else {
                throw new Error(connectResponse?.error || '');
              }
            }
            this.commandOutput += '\n';
          } catch (error) {
            this.connecting = false;
            if (cacheLoaded) {
              this.$message.warning(`: ${error.message}`);
              this.commandOutput += `\n: ${error.message}`;
              // 
              this.completeInitialization(cacheLoaded);
              return true;
            } else {
              throw error;
            }
          }
        } else {
          this.commandOutput += '\n';
          this.initializationSteps[1].done = true;
          this.initStepActive = 2;
        }
        
        // 3: 
        this.scriptCheckLoading = true;
        if (this.isServerOnline) {
          try {
            this.commandOutput += '\n...';
            const scriptResponse = await this.checkScriptExists(this.serverId);
            if (scriptResponse && scriptResponse.success) {
              this.scriptExists = scriptResponse.exists;
              if (this.scriptExists) {
                this.commandOutput += '\nNftato';
              } else {
                this.commandOutput += '\nNftato';
              }
            } else {
              this.$message.warning('');
              this.commandOutput += '\n';
              // 
              this.scriptExists = false;
            }
          } catch (error) {
            console.error(':', error);
            this.commandOutput += `\n: ${error.message}`;
            // 
            this.scriptExists = false;
          } finally {
            this.scriptCheckLoading = false;
          }
        } else if (cacheLoaded) {
          // 
          this.scriptExists = true;
          this.scriptCheckLoading = false;
        }
        
        // 
        this.completeInitialization(cacheLoaded);
        return true;
      } catch (error) {
        this.loading = false;
        this.deploying = false;
        this.connecting = false;
        this.scriptCheckLoading = false;
        this.commandOutput += `\n: ${error.message}`;
        this.$message.error(`: ${error.message}`);
        return false;
      }
    },
    
    // 
    completeInitialization(cacheLoaded) {
      this.initializationSteps[2].done = true;
      this.initStepActive = 3;
      this.initializationSteps[3].done = true;
      this.isInitialized = true;
      this.loading = false;
      
      // 
      if (this.isServerOnline && this.scriptExists) {
        this.refreshAllData();
      } else if (cacheLoaded) {
        // 
        this.loadCachedData();
      }
    },
    
    // 
    async deployScript() {
      if (!this.isServerOnline) {
        this.$message.error('');
        return;
      }
      
      try {
        this.deploying = true;
        this.deployLogs = [];
        this.deployComplete = false;
        this.deploySuccess = false;
        
        // WebSocket
        this.initWebSocket();
        
        // WebSocket
        const response = await this.deployIptatoWithWebSocket(this.serverId);
        
        if (!response || !response.success) {
          throw new Error(response?.error || '');
        }
        
        // WebSocket
        this.deployLogs.push({
          type: 'log',
          message: '...'
        });
        
      } catch (error) {
        this.deployComplete = true;
        this.deploySuccess = false;
        this.deploying = false;
        this.deployLogs.push({
          type: 'error',
          message: `: ${error.message}`
        });
        this.$message.error(`: ${error.message}`);
      }
    },
    
    // WebSocket
    initWebSocket() {
      // 
      if (this.socket) {
        this.socket.disconnect();
      }
      
      // Socket.IO
      const wsUrl = window.location.origin;
      this.socket = io(wsUrl);
      
      // 
      this.socket.on('connect', () => {
        console.log('WebSocket');
        this.deployLogs.push({
          type: 'log',
          message: ' '
        });
      });
      
      // 
      this.socket.on('deploy_start', (data) => {
        console.log(':', data);
        if (data && data.serverId === this.serverId) {
          this.deployRoomId = data.roomId;
          this.socket.emit('join_deploy_room', { roomId: this.deployRoomId });
          
          // 
          this.socket.on(this.deployRoomId, (logData) => {
            this.handleDeployLog(logData);
          });
        }
      });
      
      // 
      this.socket.on('connect_error', (error) => {
        console.error('WebSocket:', error);
        this.deployLogs.push({
          type: 'error',
          message: `WebSocket: ${error.message}`
        });
      });
      
      // 
      this.socket.on('disconnect', () => {
        console.log('WebSocket');
      });
    },
    
    // 
    handleDeployLog(logData) {
      if (!logData) return;
      
      // 
      this.deployLogs.push(logData);
      
      // 
      this.$nextTick(() => {
        if (this.$refs.terminalBody) {
          this.$refs.terminalBody.scrollTop = this.$refs.terminalBody.scrollHeight;
        }
      });
      
      // 
      if (logData.type === 'complete') {
        this.deployComplete = true;
        this.deploySuccess = true;
        this.deploying = false;
        
        // 
        this.scriptExists = true;
        
        // 
        setTimeout(() => {
          if (this.socket && this.deployRoomId) {
            this.socket.off(this.deployRoomId);
          }
        }, 1000);
      } else if (logData.type === 'error') {
        this.deployComplete = true;
        this.deploySuccess = false;
        this.deploying = false;
        
        // 
        setTimeout(() => {
          if (this.socket && this.deployRoomId) {
            this.socket.off(this.deployRoomId);
          }
        }, 1000);
      }
    },
    
    // 
    retryDeploy() {
      this.deployScript();
    },
    
    // 
    refreshAllData() {
      if (!this.isServerOnline || !this.scriptExists) return;
      
      // 
      if (this.activeTab === 'inbound') {
        this.refreshSSHPort();
        this.refreshInboundPorts();
        this.refreshInboundIPs();
      } else if (this.activeTab === 'outbound') {
        this.refreshBlockList();
      } else if (this.activeTab === 'ddos') {
        this.refreshDefenseStatus();
      }
    },
    handleInvalidServerId() {
      this.commandOutput = 'ID';
      this.$message.error('ID');
    },
    handleInitializationFailure() {
      this.$message.warning('');
      this.commandOutput += '\n';
    },
    handleInitializationError(error) {
      this.$message.error(`: ${error.message}`);
      this.commandOutput += `\n: ${error.message}`;
      console.error(':', error);
    },
    async checkInitialization() {
      try {
        if (!this.hasValidServerId) {
          this.commandOutput = 'ID';
          this.$message.error('ID');
          return false;
        }

        this.resetInitSteps();
        this.isInitialized = false;
        this.initStepActive = 0;

        this.commandOutput = '...';
        this.loading = true;

        // 1: 
        const serverResponse = await this.getServer(this.serverId);
        if (!serverResponse || !serverResponse.success) {
          throw new Error(serverResponse?.error || '');
        }
        this.server = serverResponse.data;
        this.initializationSteps[0].done = true;
        this.initStepActive = 1;

        // 
        const cacheLoaded = await this.loadServerCache();
        if (cacheLoaded) {
          this.commandOutput += '\n';
        }

        // 2: 
        if (!this.server.status || this.server.status !== 'online') {
          this.commandOutput += '\n...';
          this.connecting = true;
          const connectResponse = await this.connectServer(this.serverId);
          this.connecting = false;

          if (!connectResponse || !connectResponse.success) {
            // 
            if (cacheLoaded) {
              this.$message.warning('');
              this.commandOutput += '\n';
              this.initializationSteps[1].done = true;
              this.initStepActive = 2;
              this.initializationSteps[2].done = true;
              this.initStepActive = 3;
              this.initializationSteps[3].done = true;
              this.isInitialized = true;
              this.loading = false;
              return true;
            } else {
              throw new Error(connectResponse?.error || '');
            }
          }
          this.commandOutput += '\n';
        } else {
          // 
          this.commandOutput += '\n';
          this.initializationSteps[1].done = true;
          this.initStepActive = 2;
        }

        // 3:  - 
        if (!cacheLoaded || this.deploying) {
          this.commandOutput += '\n...';
          this.deploying = true;
          try {
            const deployResponse = await this.deployIptato(this.serverId);
            this.deploying = false;

            if (!deployResponse || !deployResponse.success) {
              const errorMsg = deployResponse?.error || '';
              this.commandOutput += `\n: ${errorMsg}`;

              if (errorMsg.includes('500') || errorMsg.includes('')) {
                this.commandOutput += '\n';
                this.commandOutput += '\n1. ';
                this.commandOutput += '\n2. ';
                this.commandOutput += '\n3. ';
                this.commandOutput += '\n\n';
                this.commandOutput += '\n- ';
                this.commandOutput += '\n- ';
                this.commandOutput += '\n- ';
              }

              this.$message.error(`: ${errorMsg}`);
              throw new Error(errorMsg);
            }

            this.commandOutput += '\n';
          } catch (deployError) {
            this.deploying = false;
            this.commandOutput += `\n: ${deployError.message}`;

            // 
            if (cacheLoaded) {
              this.$message.warning('');
              this.commandOutput += '\n';
            } else {
              throw deployError;
            }
          }
        } else {
          // 
          this.commandOutput += '\n';
        }

        this.initializationSteps[2].done = true;
        this.initStepActive = 3;

        // 4: 
        this.initializationSteps[3].done = true;
        this.isInitialized = true;
        this.loading = false;

        // 
        if (this.isServerOnline) {
          // 
          const dataToRefresh = [];

          if (!this.dataLoaded.blockList) {
            dataToRefresh.push('blockList');
          }

          if (!this.dataLoaded.sshPortStatus) {
            dataToRefresh.push('sshPortStatus');
          }

          if (!this.dataLoaded.inboundPorts) {
            dataToRefresh.push('inboundPorts');
          }

          if (!this.dataLoaded.inboundIPs) {
            dataToRefresh.push('inboundIPs');
          }

          // 
          if (dataToRefresh.length > 0) {
            setTimeout(() => this.refreshSelectedData(dataToRefresh), 500);
          }
        }

        return true;
      } catch (error) {
        this.loading = false;
        this.deploying = false;
        this.connecting = false;
        this.commandOutput += `\n: ${error.message}`;
        this.$message.error(`: ${error.message}`);
        return false;
      }
    },
    resetInitSteps() {
      this.initializationSteps.forEach(step => step.done = false);
    },
    async refreshBlockList() {
      if (!this.hasValidServerId) {
        this.$message.error('ID');
        return;
      }

      // 
      if (this.loadingBlockList) {
        return;
      }

      const now = Date.now();
      if (this.dataCache.blockList &&
        (now - this.cacheTimestamps.blockList) < this.cacheTTL.blockList) {
        this.blockList = this.dataCache.blockList;
        console.log('');
        return;
      }

      let retries = 0;
      const maxRetries = this.retryConfig.maxRetries;

      while (retries <= maxRetries) {
        try {
          this.loadingBlockList = true; // loading
          const response = await this.getBlockList(this.serverId);

          if (response && response.success) {
            this.blockList = response.data || '';
            this.dataCache.blockList = this.blockList;
            this.cacheTimestamps.blockList = now;
            this.dataLoaded.blockList = true;

            // 
            await this.updateServerCacheItem('blockList', this.blockList);
            break; // 
          } else {
            if (retries < maxRetries && this.retryConfig.enabled) {
              retries++;
              this.commandOutput += `\n${retries}...`;
              await new Promise(resolve => setTimeout(resolve, this.retryConfig.delay));
            } else {
              this.$message.warning(response?.error || '');
              this.blockList = '';
              break;
            }
          }
        } catch (error) {
          if (retries < maxRetries && this.retryConfig.enabled) {
            retries++;
            this.commandOutput += `\n${retries}...`;
            await new Promise(resolve => setTimeout(resolve, this.retryConfig.delay));
          } else {
            this.$message.error(`: ${error.message}`);
            this.blockList = `: ${error.message}`;
            break;
          }
        } finally {
          if (retries >= maxRetries || !this.retryConfig.enabled) {
            this.loadingBlockList = false;
          }
        }
      }

      this.loadingBlockList = false;
    },
    async refreshSSHPort() {
      if (!this.hasValidServerId) {
        this.$message.error('IDSSH');
        return;
      }

      const now = Date.now();
      if (this.dataCache.sshPortStatus &&
        (now - this.cacheTimestamps.sshPortStatus) < this.cacheTTL.sshPortStatus) {
        this.sshPortStatus = this.dataCache.sshPortStatus;
        console.log('SSH');
        return;
      }

      let retries = 0;
      const maxRetries = this.retryConfig.maxRetries;

      while (retries <= maxRetries) {
        try {
          this.loadingSSHPort = true; // loading
          const response = await this.getSSHPort(this.serverId);

          if (response && response.success) {
            this.sshPortStatus = response.data || 'SSH';
            this.dataCache.sshPortStatus = this.sshPortStatus;
            this.cacheTimestamps.sshPortStatus = now;
            this.dataLoaded.sshPortStatus = true;

            // 
            await this.updateServerCacheItem('sshPortStatus', this.sshPortStatus);

            try {
              const sshData = response.data;
              if (sshData && typeof sshData === 'string') {
                const portMatch = sshData.match(/SSH\s*[:]\s*(\d+)/i) ||
                  sshData.match(/\s*[:]\s*(\d+)/i) ||
                  sshData.match(/port\s*[:]\s*(\d+)/i);
                if (portMatch && portMatch[1]) {
                  this.sshPort = parseInt(portMatch[1], 10);
                  console.log(`SSH: ${this.sshPort}`);
                }
              }
            } catch (parseError) {
              console.error('SSH:', parseError);
              if (this.server && this.server.port) {
                this.sshPort = this.server.port;
                console.log(`: ${this.sshPort}`);
              }
            }
            break; // 
          } else {
            if (retries < maxRetries && this.retryConfig.enabled) {
              retries++;
              this.commandOutput += `\nSSH${retries}...`;
              await new Promise(resolve => setTimeout(resolve, this.retryConfig.delay));
            } else {
              this.$message.warning(response?.error || 'SSH');
              this.sshPortStatus = 'SSH';
              break;
            }
          }
        } catch (error) {
          if (retries < maxRetries && this.retryConfig.enabled) {
            retries++;
            this.commandOutput += `\nSSH${retries}...`;
            await new Promise(resolve => setTimeout(resolve, this.retryConfig.delay));
          } else {
            this.$message.error(`SSH: ${error.message}`);
            this.sshPortStatus = `: ${error.message}`;
            break;
          }
        } finally {
          if (retries >= maxRetries || !this.retryConfig.enabled) {
            this.loadingSSHPort = false;
          }
        }
      }

      this.loadingSSHPort = false;
    },
    async refreshInboundPorts() {
      if (!this.hasValidServerId) {
        this.$message.error('ID');
        return;
      }

      const now = Date.now();
      if (this.dataCache.inboundPorts &&
        (now - this.cacheTimestamps.inboundPorts) < this.cacheTTL.inboundPorts) {
        this.inboundPorts = this.dataCache.inboundPorts;
        console.log('');
        return;
      }

      try {
        this.loadingPorts = true; // loading
        const response = await this.getInboundPorts(this.serverId);

        if (response && response.success) {
          const data = response.data || { tcp: [], udp: [] };

          const uniquePorts = [...new Set([...(data.tcp || []), ...(data.udp || [])])];
          const formattedPorts = uniquePorts.map(port => ({
            port,
            protocol: 'TCP|UDP'
          }));

          console.log(':', formattedPorts);
          this.inboundPorts = formattedPorts;
          this.dataCache.inboundPorts = formattedPorts;
          this.cacheTimestamps.inboundPorts = now;
          this.dataLoaded.inboundPorts = true;

          // 
          await this.updateServerCacheItem('inboundPorts', response.data || { tcp: [], udp: [] });

          if (formattedPorts.length === 0) {
            this.commandOutput = '';
          } else {
            this.commandOutput = ` ${formattedPorts.length} `;
          }
        } else {
          this.$message.warning(response?.error || '');
          this.inboundPorts = [];
          this.commandOutput = `: ${response?.error || ''}`;
        }
      } catch (error) {
        this.$message.error(`: ${error.message}`);
        this.inboundPorts = [];
        this.commandOutput = `: ${error.message}`;
      } finally {
        this.loadingPorts = false;
      }
    },
    async refreshInboundIPs() {
      if (!this.hasValidServerId) {
        this.$message.error('IDIP');
        return;
      }

      const now = Date.now();
      if (this.dataCache.inboundIPs &&
        (now - this.cacheTimestamps.inboundIPs) < this.cacheTTL.inboundIPs) {
        this.inboundIPs = this.dataCache.inboundIPs;
        console.log('IP');
        return;
      }

      try {
        this.loadingIPs = true; // loading
        const response = await this.getInboundIPs(this.serverId);

        if (!response || !response.success) {
          if (response && response.error && response.error.includes('')) {
            this.$message.error('');
            this.resetInitSteps();
            this.isInitialized = false;
          } else if (response && response.error && response.error.includes('')) {
            this.$message.error('');
            this.resetInitSteps();
            this.isInitialized = false;
          } else {
            this.$message.warning(response?.error || 'IP');
          }
          this.inboundIPs = [];
          this.commandOutput = `IP: ${response?.error || ''}`;
          return;
        }

        const ipData = response.data || [];

        if (Array.isArray(ipData)) {
          this.inboundIPs = ipData.map(ip => typeof ip === 'string' ? { ip } : ip);
          this.dataCache.inboundIPs = this.inboundIPs;
          this.cacheTimestamps.inboundIPs = now;
          this.dataLoaded.inboundIPs = true;

          // 
          await this.updateServerCacheItem('inboundIPs', response.data || []);

          if (ipData.length === 0) {
            this.commandOutput = 'IP';
          } else {
            this.commandOutput = `IP ${ipData.length} IP`;
          }
        } else {
          this.$message.warning('IP');
          this.inboundIPs = [];
          this.commandOutput = 'IP';
        }
      } catch (error) {
        this.$message.error(`IP: ${error.message}`);
        this.inboundIPs = [];
        this.commandOutput = `IP: ${error.message}`;
      } finally {
        this.loadingIPs = false;
      }
    },
