<template>
  <div class="servers-container">
    <div class="page-header">
      <h1></h1>
      <el-button type="primary" @click="showAddServerDialog"></el-button>
    </div>

    <!--  -->
    <el-alert
      v-if="isServerRestarted"
      title=""
      type="warning"
      :closable="true"
      show-icon
      style="margin-bottom: 15px;"
    >
      <template slot="title">
        <span style="font-weight: bold;"></span>
      </template>
      <div>
        
        <el-button size="mini" type="primary" @click="batchConnect" style="margin-left: 10px;" :disabled="!hasOfflineServers"></el-button>
      </div>
    </el-alert>

    <!--  -->
    <div v-if="servers.length === 0 && !loading" class="empty-state">
      <el-empty description="" :image-size="200">
        <el-button type="primary" @click="showAddServerDialog"></el-button>
      </el-empty>
    </div>

    <el-table
      v-else
      v-loading="loading"
      :data="servers"
      border
      style="width: 100%"
    >
      <el-table-column
        prop="name"
        label=""
        width="180"
      ></el-table-column>
      <el-table-column
        prop="host"
        label=""
        width="180"
      ></el-table-column>
      <el-table-column
        prop="port"
        label="SSH"
        width="100"
      ></el-table-column>
      <el-table-column
        prop="username"
        label=""
        width="120"
      ></el-table-column>
      <el-table-column
        prop="status"
        label=""
        width="160"
      >
        <template slot-scope="scope">
          <div class="status-container">
            <el-tag
              :type="getStatusTagType(scope.row.status)"
            >
              {{ statusText[scope.row.status] }}
            </el-tag>
            <el-button 
              type="text" 
              icon="el-icon-refresh" 
              circle 
              size="mini" 
              @click="checkServerStatus(scope.row)"
              :loading="checkingServers[scope.row._id]"
              class="refresh-button"
            ></el-button>
            <el-popover
              v-if="errorReasons[scope.row._id]"
              placement="top-start"
              title=""
              width="300"
              trigger="hover"
            >
              <div>
                <p><i class="el-icon-warning" style="color: #E6A23C;"></i> {{ errorReasons[scope.row._id] }}</p>
                <el-divider></el-divider>
                <p></p>
                <el-button size="mini" type="primary" @click="handleReconnect(scope.row)"></el-button>
                <el-button size="mini" @click="checkServerStatus(scope.row)"></el-button>
                <el-button size="mini" type="success" @click="handleConnectionRetry(scope.row)"></el-button>
              </div>
              <el-badge slot="reference" is-dot type="danger"></el-badge>
            </el-popover>
          </div>
          <div v-if="scope.row.lastChecked" class="status-time">
            : {{ formatTime(scope.row.lastChecked) }}
          </div>
          <!--  -->
          <div v-if="scope.row.status === 'error' && errorReasons[scope.row._id] && errorReasons[scope.row._id].includes('')" class="sync-warning">
            <el-link type="warning" @click="handleConnectionRetry(scope.row)">
              <i class="el-icon-warning-outline"></i> 
            </el-link>
          </div>
        </template>
      </el-table-column>
      <el-table-column
        label=""
      >
        <template slot-scope="scope">
          <div class="operation-buttons">
            <el-button
              size="mini"
              @click="handleEdit(scope.row)"
              icon="el-icon-edit"
            ></el-button>
            <el-button
              v-if="scope.row.status !== 'online' && scope.row.status !== 'connecting' && scope.row.status !== 'disconnecting'"
              size="mini"
              type="success"
              @click="handleConnect(scope.row)"
              :loading="connectingServers[scope.row._id]"
              icon="el-icon-connection"
            ></el-button>
            <el-button
              v-else-if="scope.row.status === 'online'"
              size="mini"
              type="warning"
              @click="handleDisconnect(scope.row)"
              :loading="disconnectingServers[scope.row._id]"
              icon="el-icon-close"
            ></el-button>
            <el-button
              v-else
              size="mini"
              disabled
            >{{ statusText[scope.row.status] }}</el-button>
            <el-button
              v-if="scope.row.status === 'online'"
              size="mini"
              type="primary"
              @click="handleManageRules(scope.row)"
              icon="el-icon-setting"
            ></el-button>
            <el-button
              size="mini"
              type="danger"
              @click="handleDelete(scope.row)"
              icon="el-icon-delete"
            ></el-button>
          </div>
        </template>
      </el-table-column>
    </el-table>

    <!--  -->
    <div v-if="servers.length > 0" class="batch-actions">
      <el-card shadow="hover">
        <div slot="header" class="clearfix">
          <span><i class="el-icon-s-operation"></i> </span>
        </div>
        <div class="batch-buttons">
          <el-button size="small" type="success" @click="batchConnect" :disabled="!hasOfflineServers" icon="el-icon-connection"> <span v-if="hasOfflineServers" class="count-badge">({{ getOfflineCount() }})</span></el-button>
          <el-button size="small" type="warning" @click="batchDisconnect" :disabled="!hasOnlineServers" icon="el-icon-close"> <span v-if="hasOnlineServers" class="count-badge">({{ getOnlineCount() }})</span></el-button>
          <el-button size="small" type="info" @click="checkAllServersStatus" icon="el-icon-refresh"></el-button>
        </div>
      </el-card>
    </div>

    <!-- / -->
    <el-dialog
      :title="isEdit ? '' : ''"
      :visible.sync="dialogVisible"
      width="50%"
    >
      <server-form
        :is-edit="isEdit"
        :server-data="currentServer"
        @submit="handleFormSubmit"
        ref="serverForm"
      ></server-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false"></el-button>
        <el-button v-if="!isEdit" type="primary" @click="handleTestConnection"></el-button>
        <el-button type="primary" @click="$refs.serverForm.submitForm()"></el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import ServerForm from '@/components/ServerForm.vue';
import { mapActions } from 'vuex';

export default {
  name: 'ServersView',
  components: {
    ServerForm
  },
  data() {
    return {
      loading: false,
      servers: [],
      dialogVisible: false,
      isEdit: false,
      currentServer: null,
      statusText: {
        'online': '',
        'offline': '',
        'error': '',
        'connecting': '',
        'disconnecting': '',
        'restarting': ''
      },
      disconnectingServers: {},
      connectingServers: {},
      checkingServers: {},
      statusCheckInterval: null,
      heartbeatIntervals: {},  // 
      lastStateTime: {},
      errorReasons: {}, // 
      reconnectCounters: {}, // 
      sessionId: '', // 
      isServerRestarted: false, // 
      isRetrying: false // 
    };
  },
  computed: {
    hasOnlineServers() {
      return this.servers.some(server => server.status === 'online');
    },
    hasOfflineServers() {
      return this.servers.some(server => server.status === 'offline' || server.status === 'error');
    }
  },
  created() {
    // 
    this.checkPanelRestart();
    this.fetchServers();
    // 
    this.loadCachedStates();
    // 30
    this.statusCheckInterval = setInterval(() => {
      this.checkAllServersStatus();
    }, 30000);
  },
  mounted() {
    // 
    setTimeout(async () => {
      // DOM
      if (!this.isServerRestarted) {
        // 
        await this.verifyOnlineServersStatus();
      }
      
      // 
      this.autoFixInconsistentStatus();
    }, 1000);
  },
  beforeDestroy() {
    // 
    if (this.statusCheckInterval) {
      clearInterval(this.statusCheckInterval);
    }
    
    // 
    Object.keys(this.heartbeatIntervals).forEach(serverId => {
      clearInterval(this.heartbeatIntervals[serverId]);
    });
  },
  methods: {
    ...mapActions('servers', [
      'getAllServers',
      'createServer',
      'updateServer',
      'deleteServer',
      'connectServer',
      'disconnectServer',
      'checkStatus',
      'testConnection',
      'sendHeartbeat',
      'getPanelStatus',  // API
      'getServerLogs'  // API
    ]),
    async fetchServers() {
      this.loading = true;
      try {
        const response = await this.getAllServers();
        this.servers = response.data;
        
        // 
        await this.verifyOnlineServersStatus();
        
        // 
        this.saveStatesToCache();
      } catch (error) {
        this.$message.error(': ' + error.message);
        
        // 
        localStorage.removeItem('serverStates');
      } finally {
        this.loading = false;
      }
    },
    // 
    async verifyOnlineServersStatus() {
      const onlineServers = this.servers.filter(s => s.status === 'online');
      if (onlineServers.length === 0) return;
      
      // 
      this.loading = true;
      
      try {
        // 
        const verifyPromises = onlineServers.map(async (server) => {
          try {
            const actualStatus = await this.verifyServerStatus(server);
            
            // 
            if (actualStatus !== 'online' && server.status === 'online') {
              this.isServerRestarted = true;
              
              // 
              const index = this.servers.findIndex(s => s._id === server._id);
              if (index !== -1) {
                // 
                this.$set(this.servers[index], 'statusChanged', true);
                this.$set(this.servers[index], 'status', actualStatus);
                this.$set(this.servers[index], 'lastChecked', Date.now());
                
                // 2
                setTimeout(() => {
                  this.$set(this.servers[index], 'statusChanged', false);
                }, 2000);
              }
            }
          } catch (error) {
            console.error(` ${server.name} :`, error);
            // 
            const index = this.servers.findIndex(s => s._id === server._id);
            if (index !== -1) {
              this.$set(this.servers[index], 'status', 'error');
              this.$set(this.servers[index], 'statusChanged', true);
              this.$set(this.errorReasons, server._id, '');
              this.$set(this.servers[index], 'lastChecked', Date.now());
              
              // 2
              setTimeout(() => {
                this.$set(this.servers[index], 'statusChanged', false);
              }, 2000);
            }
          }
        });
        
        // 
        await Promise.all(verifyPromises);
      } finally {
        this.loading = false;
      }
      
      // 
      if (this.isServerRestarted) {
        // 
      }
    },
    // 
    async checkPanelRestart() {
      try {
        // ID
        const storedSessionId = localStorage.getItem('panelSessionId');
        
        // ID
        const response = await this.getPanelStatus();
        if (response && response.data && response.data.sessionId) {
          const currentSessionId = response.data.sessionId;
          this.sessionId = currentSessionId;
          
          // ID
          localStorage.setItem('panelSessionId', currentSessionId);
          
          // ID
          if (storedSessionId && storedSessionId !== currentSessionId) {
            this.isServerRestarted = true;
            this.handlePanelRestart();
            return true;
          }
        }
        return false;
      } catch (error) {
        console.error(':', error);
        // 
        this.isServerRestarted = true;
        this.handlePanelRestart();
        return true;
      }
    },
    
    // 
    async handlePanelRestart() {
      // 
      this.$notify({
        title: '',
        message: '...',
        type: 'warning',
        duration: 0,
        showClose: true
      });
      
      // 
      localStorage.removeItem('serverStates');
      
      // 
      setTimeout(async () => {
        // 
        const onlineServers = this.servers.filter(s => s.status === 'online');
        if (onlineServers.length > 0) {
          try {
            // 
            this.loading = true;
            
            // 
            try {
              await this.$confirm(
                ` ${onlineServers.length} `, 
                '', 
                {
                  confirmButtonText: '',
                  cancelButtonText: '',
                  type: 'warning',
                  closeOnClickModal: false
                }
              );
              
              // 
              for (const server of onlineServers) {
                try {
                  await this.handleReconnect(server);
                } catch (err) {
                  console.error(':', err);
                }
              }
              
              this.$message.success('');
            } catch (err) {
              // 
              if (err === 'cancel') {
                this.$message.info('');
                // """"
                onlineServers.forEach(server => {
                  const index = this.servers.findIndex(s => s._id === server._id);
                  if (index !== -1) {
                    this.$set(this.servers[index], 'status', 'error');
                    this.$set(this.errorReasons, server._id, '');
                  }
                });
              }
            }
          } finally {
            this.loading = false;
          }
        }
      }, 500);
    },
    
    // 
    saveStatesToCache() {
      const states = {};
      this.servers.forEach(server => {
        states[server._id] = {
          status: server.status,
          timestamp: Date.now(),
          sessionId: this.sessionId // ID
        };
      });
      localStorage.setItem('serverStates', JSON.stringify(states));
    },
    
    // 
    loadCachedStates() {
      // localStorage
      const cachedStates = localStorage.getItem('serverStates');
      if (!cachedStates) return;
      
      try {
        const states = JSON.parse(cachedStates);
        
        // ID
        const firstServer = Object.values(states)[0];
        if (firstServer && firstServer.sessionId && firstServer.sessionId !== this.sessionId) {
          // ID
          this.isServerRestarted = true;
          return;
        }
        
        // 10
        const now = Date.now();
        const isExpired = Object.values(states).some(state => {
          return (now - state.timestamp) > 10 * 60 * 1000; // 10
        });
        
        if (isExpired) {
          console.log('');
          return;
        }
        
        this.lastStateTime = states;
      } catch (error) {
        console.error(':', error);
      }
    },
    showAddServerDialog() {
      this.isEdit = false;
      this.currentServer = null;
      this.dialogVisible = true;
    },
    handleEdit(server) {
      this.isEdit = true;
      this.currentServer = { ...server };
      this.dialogVisible = true;
    },
    async handleTestConnection() {
      // 
      const formData = this.$refs.serverForm.getFormData();
      if (!formData) return;
      
      try {
        await this.testConnection(formData);
        this.$message.success('');
      } catch (error) {
        this.$message.error(': ' + error.message);
      }
    },
    async handleFormSubmit(formData) {
      try {
        if (this.isEdit) {
          await this.updateServer({
            id: this.currentServer._id,
            data: formData
          });
          this.$message.success('');
        } else {
          await this.createServer(formData);
          this.$message.success('');
        }
        this.dialogVisible = false;
        this.fetchServers();
      } catch (error) {
        this.$message.error(error.message);
      }
    },
    async handleDelete(server) {
      try {
        await this.$confirm(', ?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        });
        
        await this.deleteServer(server._id);
        this.$message.success('');
        this.fetchServers();
      } catch (error) {
        if (error !== 'cancel') {
          this.$message.error(': ' + error.message);
        }
      }
    },
    async verifyServerStatus(server) {
      // 
      try {
        this.$set(this.checkingServers, server._id, true);
        
        // 
        let logBasedStatus = null;
        try {
          const logResponse = await this.getServerLogs(server._id);
          if (logResponse && logResponse.data) {
            const logs = logResponse.data;
            
            // 
            if (logs.includes('SSH') || 
                logs.includes('') ||
                logs.includes('')) {
              
              console.log('');
              logBasedStatus = 'online';
            }
          }
        } catch (error) {
          console.error(':', error);
        }
        
        // 
        if (logBasedStatus === 'online') {
          // 
          const index = this.servers.findIndex(s => s._id === server._id);
          if (index !== -1 && this.servers[index].status !== 'online') {
            this.$set(this.servers[index], 'status', 'online');
            this.$set(this.servers[index], 'lastChecked', Date.now());
            this.$delete(this.errorReasons, server._id);
          }
          
          return 'online';
        }
        
        // API
        const response = await this.checkStatus(server._id);
        const actualStatus = response.data.data.status;
        const backendConnected = response.data.data.backendConnected || false;
        
        // API
        if (actualStatus === 'online' || backendConnected) {
          // 
          const index = this.servers.findIndex(s => s._id === server._id);
          if (index !== -1 && this.servers[index].status !== 'online') {
            this.$set(this.servers[index], 'status', 'online');
            this.$set(this.servers[index], 'lastChecked', Date.now());
            this.$delete(this.errorReasons, server._id);
          }
          
          return 'online';
        }
        
        // API
        const index = this.servers.findIndex(s => s._id === server._id);
        if (index !== -1 && this.servers[index].status !== actualStatus) {
          this.$set(this.servers[index], 'status', actualStatus);
          this.$message.warning(`${server.name}${this.statusText[actualStatus]}`);
        }
        
        return actualStatus;
      } catch (error) {
        console.error(':', error);
        return 'error';
      } finally {
        this.$set(this.checkingServers, server._id, false);
      }
    },
    async handleConnect(server) {
      try {
        // 
        this.$set(this.connectingServers, server._id, true);
        
        // ""
        const index = this.servers.findIndex(s => s._id === server._id);
        if (index !== -1) {
          this.$set(this.servers[index], 'status', 'connecting');
        }
        
        // 
        const connectNotification = this.$notify({
          title: '',
          message: ` ${server.name}...`,
          duration: 0,
          type: 'info'
        });
        
        // 
        const connectResult = await this.connectServer(server._id);
        console.log(':', connectResult);
        
        // 
        connectNotification.close();
        
        // 
        const serverStatus = connectResult?.serverStatus || 'unknown';
        
        if (serverStatus === 'online') {
          // API
          if (index !== -1) {
            this.$set(this.servers[index], 'status', 'online');
            this.$set(this.servers[index], 'lastChecked', Date.now());
            this.$set(this.servers[index], 'statusChanged', true);
            this.$delete(this.errorReasons, server._id);
            
            // 2
            setTimeout(() => {
              this.$set(this.servers[index], 'statusChanged', false);
            }, 2000);
          }
          
          this.$message.success('');
          
          // 
          this.startHeartbeat(server);
        } else {
          // 
          console.log('...');
          
          // 1
          await new Promise(resolve => setTimeout(resolve, 1000));
          
          // 
          try {
            const statusResponse = await this.checkStatus(server._id);
            console.log(':', statusResponse);
            
            const actualStatus = statusResponse?.data?.data?.status || 'error';
            const backendConnected = statusResponse?.data?.data?.backendConnected || false;
            
            if (actualStatus === 'online' || backendConnected) {
              // UI
              if (index !== -1) {
                this.$set(this.servers[index], 'status', 'online');
                this.$set(this.servers[index], 'lastChecked', Date.now());
                this.$set(this.servers[index], 'statusChanged', true);
                this.$delete(this.errorReasons, server._id);
                
                // 2
                setTimeout(() => {
                  this.$set(this.servers[index], 'statusChanged', false);
                }, 2000);
              }
              
              this.$message.success('');
              
              // 
              this.startHeartbeat(server);
            } else {
              // 
              this.$message.error('');
              
              // 
              try {
                const logsResponse = await this.getServerLogs(server._id);
                console.log(':', logsResponse);
                
                const logs = logsResponse?.data?.data || '';
                const connectionStatus = logsResponse?.data?.connectionStatus || {};
                
                // 
                if (logs.includes('') || connectionStatus.connectionValid) {
                  // 
                  if (index !== -1) {
                    this.$set(this.servers[index], 'status', 'online');
                    this.$set(this.servers[index], 'lastChecked', Date.now());
                    this.$set(this.servers[index], 'statusChanged', true);
                    this.$delete(this.errorReasons, server._id);
                  }
                  
                  this.$message.success('');
                  
                  // 
                  this.startHeartbeat(server);
                } else {
                  // 
                  if (index !== -1) {
                    this.$set(this.servers[index], 'status', 'error');
                    this.$set(this.errorReasons, server._id, '');
                  }
                }
              } catch (logError) {
                console.error(':', logError);
                
                // 
                if (index !== -1) {
                  this.$set(this.servers[index], 'status', 'error');
                  this.$set(this.errorReasons, server._id, '');
                }
              }
            }
          } catch (statusError) {
            console.error(':', statusError);
            
            // 
            if (index !== -1) {
              this.$set(this.servers[index], 'status', 'error');
              this.$set(this.errorReasons, server._id, '');
            }
          }
        }
      } catch (error) {
        // 
        const errorMsg = this.parseErrorMessage(error);
        this.$set(this.errorReasons, server._id, errorMsg);
        
        this.$message.error(': ' + errorMsg);
        
        // 
        const index = this.servers.findIndex(s => s._id === server._id);
        if (index !== -1) {
          this.$set(this.servers[index], 'status', 'error');
          this.$set(this.servers[index], 'lastChecked', Date.now());
        }
      } finally {
        this.$set(this.connectingServers, server._id, false);
        this.saveStatesToCache();
      }
    },
    async handleDisconnect(server) {
      try {
        // 
        this.$set(this.disconnectingServers, server._id, true);
        
        // ""
        const index = this.servers.findIndex(s => s._id === server._id);
        if (index !== -1) {
          this.$set(this.servers[index], 'status', 'disconnecting');
        }
        
        // 
        this.stopHeartbeat(server._id);
        
        // 
        const disconnectNotification = this.$notify({
          title: '',
          message: ` ${server.name} ...`,
          duration: 0,
          type: 'warning'
        });
        
        // 
        await this.disconnectServer(server._id);
        disconnectNotification.close();
        this.$message.success('');
        
        // 
        if (index !== -1) {
          this.$set(this.servers[index], 'status', 'offline');
        }
        
        // 
        await this.fetchServers();
      } catch (error) {
        this.$message.error(': ' + error.message);
        // 
        await this.checkServerStatus(server);
      } finally {
        // 
        this.$set(this.disconnectingServers, server._id, false);
        this.saveStatesToCache();
      }
    },
    async handleManageRules(server) {
      // UI
      if (server.status === 'online') {
        this.$router.push({ name: 'rules', params: { serverId: server._id } });
        return;
      }
      
      // 
      try {
        // 
        this.$set(this.checkingServers, server._id, true);
        
        // 1. 
        let isActuallyConnected = false;
        try {
          const logResponse = await this.getServerLogs(server._id);
          if (logResponse && logResponse.data) {
            const logs = logResponse.data;
            
            // 
            if (logs.includes('SSH') || 
                logs.includes('') ||
                logs.includes('')) {
              
              console.log('');
              isActuallyConnected = true;
              
              // 
              const index = this.servers.findIndex(s => s._id === server._id);
              if (index !== -1 && this.servers[index].status !== 'online') {
                this.$set(this.servers[index], 'status', 'online');
                this.$set(this.servers[index], 'lastChecked', Date.now());
                this.$delete(this.errorReasons, server._id);
                
                // 
                this.$message.info(` ${server.name} `);
                
                // 
                setTimeout(() => {
                  this.$router.push({ name: 'rules', params: { serverId: server._id } });
                }, 500);
                return;
              }
            }
          }
        } catch (error) {
          console.error(':', error);
        }
        
        // 
        if (isActuallyConnected) {
          this.$router.push({ name: 'rules', params: { serverId: server._id } });
          return;
        }
        
        // 2. API
        const statusResponse = await this.checkStatus(server._id);
        const actualStatus = statusResponse?.data?.data?.status || 'error';
        const backendConnected = statusResponse?.data?.data?.backendConnected || false;
        
        // API
        if (actualStatus === 'online' || backendConnected) {
          // 
          const index = this.servers.findIndex(s => s._id === server._id);
          if (index !== -1) {
            this.$set(this.servers[index], 'status', 'online');
            this.$set(this.servers[index], 'lastChecked', Date.now());
          }
          
          // 
          this.$router.push({ name: 'rules', params: { serverId: server._id } });
          return;
        }
        
        // 
        const errorReason = this.errorReasons[server._id] || '';
        
        this.$confirm(`${errorReason}?`, '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          this.handleConnect(server).then(() => {
            // 
            this.$router.push({ name: 'rules', params: { serverId: server._id } });
          });
        }).catch(() => {});
      } catch (error) {
        console.error(':', error);
        
        // 
        this.$confirm(`?`, '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          this.handleConnect(server).then(() => {
            this.$router.push({ name: 'rules', params: { serverId: server._id } });
          });
        }).catch(() => {});
      } finally {
        this.$set(this.checkingServers, server._id, false);
      }
    },
    async checkServerStatus(server) {
      try {
        this.$set(this.checkingServers, server._id, true);
        const response = await this.checkStatus(server._id);
        // 
        const index = this.servers.findIndex(s => s._id === server._id);
        if (index !== -1) {
          this.$set(this.servers[index], 'status', response.data.data.status);
          this.$set(this.servers[index], 'lastChecked', Date.now());
        }
        // 
        this.saveStatesToCache();
      } catch (error) {
        console.error(':', error);
      } finally {
        this.$set(this.checkingServers, server._id, false);
      }
    },
    async checkAllServersStatus() {
      for (const server of this.servers) {
        await this.checkServerStatus(server);
      }
    },
    getStatusTagType(status) {
      switch (status) {
        case 'online':
          return 'success';
        case 'error':
          return 'danger';
        case 'connecting':
          return 'info';
        case 'disconnecting':
          return 'warning';
        default:
          return '';
      }
    },
    // 
    async batchConnect() {
      const offlineServers = this.servers.filter(server => server.status === 'offline' || server.status === 'error');
      if (offlineServers.length === 0) return;
      
      try {
        await this.$confirm(`${offlineServers.length}?`, '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'info'
        });
        
        for (const server of offlineServers) {
          await this.handleConnect(server);
        }
        
        this.$message.success('');
      } catch (error) {
        if (error !== 'cancel') {
          this.$message.error(': ' + error.message);
        }
      }
    },
    // 
    async batchDisconnect() {
      const onlineServers = this.servers.filter(server => server.status === 'online');
      if (onlineServers.length === 0) return;
      
      try {
        await this.$confirm(`${onlineServers.length}?`, '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        });
        
        for (const server of onlineServers) {
          await this.handleDisconnect(server);
        }
        
        this.$message.success('');
      } catch (error) {
        if (error !== 'cancel') {
          this.$message.error(': ' + error.message);
        }
      }
    },
    // 
    startHeartbeat(server) {
      if (this.heartbeatIntervals[server._id]) {
        clearInterval(this.heartbeatIntervals[server._id]);
      }
      
      //  - 
      setTimeout(async () => {
        try {
          // 
          const statusResult = await this.checkStatus(server._id);
          if (statusResult && statusResult.data && statusResult.data.status === 'error') {
            // 
            if (statusResult.logs && 
               (statusResult.logs.includes('') || 
                statusResult.logs.includes('SSH') || 
                statusResult.logs.includes('SSH'))) {
              console.log('');
              const index = this.servers.findIndex(s => s._id === server._id);
              if (index !== -1) {
                this.$set(this.servers[index], 'status', 'online');
              }
            }
          }
        } catch (error) {
          console.error(':', error);
        }
      }, 2000);
      
      // 10
      this.heartbeatIntervals[server._id] = setInterval(async () => {
        if (!server || server.status !== 'online') {
          this.stopHeartbeat(server._id);
          return;
        }
        
        try {
          const response = await this.sendHeartbeat(server._id);
          if (response && response.data && response.data.status === 'success') {
            // 
            if (this.reconnectCounters[server._id]) {
              this.reconnectCounters[server._id] = 0;
            }
          } else {
            // 
            await this.handleHeartbeatFailure(server);
          }
        } catch (error) {
          // 
          try {
            const statusResponse = await this.checkStatus(server._id);
            // 
            if (statusResponse && statusResponse.data && 
                (statusResponse.data.status === 'online' || 
                 statusResponse.data.backendConnected)) {
              console.log('');
              return;
            }
          } catch (checkError) {
            console.error(':', checkError);
          }
          
          // 
          await this.handleHeartbeatFailure(server);
        }
      }, 10000);
    },
    
    // 
    stopHeartbeat(serverId) {
      if (this.heartbeatIntervals[serverId]) {
        clearInterval(this.heartbeatIntervals[serverId]);
        delete this.heartbeatIntervals[serverId];
      }
    },
    
    // 
    async handleHeartbeatFailure(server) {
      const index = this.servers.findIndex(s => s._id === server._id);
      if (index === -1) return;
      
      // 
      try {
        const logResponse = await this.getServerLogs(server._id);
        
        // 
        if (logResponse && logResponse.data) {
          const logs = logResponse.data;
          
          if (logs.includes('SSH') || 
              logs.includes('') ||
              logs.includes('')) {
                
            console.log('');
            
            // 
            if (this.servers[index].status !== 'online') {
              this.$set(this.servers[index], 'status', 'online');
              this.$set(this.servers[index], 'lastChecked', Date.now());
              this.$delete(this.errorReasons, server._id);
              
              // 
              this.$message.info(` ${server.name} `);
            }
            
            // 
            return;
          }
        }
      } catch (error) {
        console.error(':', error);
      }
      
      // 
      if (this.servers[index].status === 'online') {
        // 
        this.$set(this.servers[index], 'status', 'error');
        this.$set(this.errorReasons, server._id, '');
        
        // 
        const errorMsg = ` ${server.name} `;
        this.$notify({
          title: '',
          message: errorMsg,
          type: 'error',
          duration: 0,
          onClick: () => {
            this.showReconnectDialog(server);
          }
        });
        
        // 
        if (!this.reconnectCounters[server._id]) {
          this.reconnectCounters[server._id] = 0;
        }
        
        // 
        if (this.reconnectCounters[server._id] === 0) {
          this.showReconnectDialog(server);
        }
        
        this.reconnectCounters[server._id]++;
      }
      
      // 
      await this.verifyServerStatus(server);
    },
    
    // 
    showReconnectDialog(server) {
      this.$confirm(` ${server.name} `, '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning',
        closeOnClickModal: false,
        closeOnPressEscape: false,
        showClose: false
      }).then(() => {
        // 
        this.handleReconnect(server);
      }).catch(() => {
        // 
        this.$message({
          type: 'info',
          message: ` ${server.name} `
        });
      });
    },
    
    // 
    async handleReconnect(server) {
      try {
        // 
        try {
          await this.disconnectServer(server._id);
        } catch (error) {
          console.log(':', error);
        }
        
        // 
        setTimeout(async () => {
          try {
            // ""
            const index = this.servers.findIndex(s => s._id === server._id);
            if (index !== -1) {
              this.$set(this.servers[index], 'status', 'connecting');
            }
            
            // 
            this.$set(this.errorReasons, server._id, null);
            
            // 
            this.$set(this.connectingServers, server._id, true);
            
            // 
            await this.connectServer(server._id);
            this.$message.success(` ${server.name} `);
            
            // 
            await this.fetchServers();
            
            // 
            const updatedServer = this.servers.find(s => s._id === server._id);
            if (updatedServer && updatedServer.status === 'online') {
              this.startHeartbeat(updatedServer);
            }
          } catch (error) {
            this.$message.error(`: ${error.message}`);
            
            // 
            const errorMsg = this.parseErrorMessage(error);
            this.$set(this.errorReasons, server._id, errorMsg);
            
            // 
            const index = this.servers.findIndex(s => s._id === server._id);
            if (index !== -1) {
              this.$set(this.servers[index], 'status', 'error');
            }
          } finally {
            this.$set(this.connectingServers, server._id, false);
          }
        }, 1000);
      } catch (error) {
        this.$message.error(`: ${error.message}`);
      }
    },
    
    // 
    showTimeoutHelpDialog(server) {
      this.$alert(`
        <strong></strong>
        <ul>
          <li></li>
          <li>SSH</li>
          <li></li>
          <li></li>
        </ul>
        <strong></strong>
        <ul>
          <li></li>
          <li>SSH</li>
          <li></li>
          <li></li>
        </ul>
        <p></p>
      `, '', {
        dangerouslyUseHTMLString: true,
        confirmButtonText: '',
        callback: () => {}
      });
    },
    
    // 
    parseErrorMessage(error) {
      let errorMsg = '';
      
      if (typeof error === 'string') {
        errorMsg = error;
      } else if (error.message) {
        errorMsg = error.message;
      }
      
      // 
      if (errorMsg.includes('timeout') || errorMsg.includes('') || errorMsg.includes('timed out')) {
        return 'SSH';
      } else if (errorMsg.includes('refused') || errorMsg.includes('')) {
        return '';
      } else if (errorMsg.includes('authentication') || errorMsg.includes('')) {
        return '';
      } else if (errorMsg.includes('not found') || errorMsg.includes('')) {
        return '';
      } else if (errorMsg.includes('handshake')) {
        return 'SSHSSH';
      } else if (errorMsg.includes('took too long')) {
        return '';
      }
      
      return `: ${errorMsg}`;
    },
    // 
    getOfflineCount() {
      return this.servers.filter(server => server.status === 'offline' || server.status === 'error').length;
    },
    
    // 
    getOnlineCount() {
      return this.servers.filter(server => server.status === 'online').length;
    },
    
    // 
    formatTime(timestamp) {
      if (!timestamp) return '';
      
      const now = new Date();
      const time = new Date(timestamp);
      const diff = Math.floor((now - time) / 1000); // 
      
      if (diff < 60) {
        return '';
      } else if (diff < 3600) {
        return `${Math.floor(diff / 60)}`;
      } else if (diff < 86400) {
        return `${Math.floor(diff / 3600)}`;
      } else {
        return `${time.getMonth() + 1}-${time.getDate()} ${time.getHours()}:${time.getMinutes()}`;
      }
    },
    // 
    async handleConnectionRetry(server) {
      try {
        // 
        if (this.isRetrying) return;
        this.isRetrying = true;
        
        // 
        this.$message.info(` ${server.name} ...`);
        
        const actualStatus = await this.forceCheckServerStatus(server);
        
        // 
        if (actualStatus === 'online') {
          this.$message.success(` ${server.name} `);
        } else if (actualStatus === 'offline') {
          this.$confirm(` ${server.name} `, '', {
            confirmButtonText: '',
            cancelButtonText: '',
            type: 'info'
          }).then(() => {
            this.handleConnect(server);
          }).catch(() => {});
        } else {
          // 
          this.checkServerLogs(server);
        }
      } catch (error) {
        this.$message.error(`: ${error.message}`);
      } finally {
        // 
        setTimeout(() => {
          this.isRetrying = false;
        }, 1000);
      }
    },
    
    // UI
    async forceCheckServerStatus(server) {
      try {
        this.$set(this.checkingServers, server._id, true);
        
        // 
        await new Promise(resolve => setTimeout(resolve, 1000));
        
        // 
        let logBasedStatus = null;
        try {
          const logResponse = await this.getServerLogs(server._id);
          if (logResponse && logResponse.data) {
            const logs = logResponse.data;
            
            if (logs.includes('SSH') || 
                logs.includes('') ||
                logs.includes('')) {
              logBasedStatus = 'online';
              console.log('');
            }
          }
        } catch (error) {
          console.error(':', error);
        }
        
        // 
        if (logBasedStatus === 'online') {
          const index = this.servers.findIndex(s => s._id === server._id);
          if (index !== -1) {
            const oldStatus = this.servers[index].status;
            this.$set(this.servers[index], 'status', 'online');
            this.$set(this.servers[index], 'lastChecked', Date.now());
            
            if (oldStatus !== 'online') {
              this.$set(this.servers[index], 'statusChanged', true);
              this.$delete(this.errorReasons, server._id);
              
              // 
              this.startHeartbeat(this.servers[index]);
              
              // 2
              setTimeout(() => {
                this.$set(this.servers[index], 'statusChanged', false);
              }, 2000);
              
              this.$message.success(` ${server.name} `);
            }
            
            this.saveStatesToCache();
            return 'online';
          }
        }
        
        // 3
        let actualStatus = 'error';
        let retryCount = 0;
        const maxRetries = 3;
        
        while (retryCount < maxRetries) {
          try {
            const response = await this.checkStatus(server._id);
            if (response && response.data && response.data.data) {
              actualStatus = response.data.data.status;
              
              // error
              // 
              if (actualStatus === 'error' && 
                 (response.data.data.backendConnected || logBasedStatus === 'online')) {
                console.log('');
                actualStatus = 'online';
                break;
              }
              
              // 
              if (actualStatus === 'online') {
                break;
              }
            }
          } catch (error) {
            console.error(` ${retryCount + 1}/${maxRetries} :`, error);
          }
          
          retryCount++;
          if (retryCount < maxRetries) {
            // 
            await new Promise(resolve => setTimeout(resolve, 1000));
          }
        }
        
        // 
        const index = this.servers.findIndex(s => s._id === server._id);
        if (index !== -1) {
          const oldStatus = this.servers[index].status;
          this.$set(this.servers[index], 'status', actualStatus);
          this.$set(this.servers[index], 'lastChecked', Date.now());
          
          // 
          if (oldStatus !== actualStatus) {
            this.$set(this.servers[index], 'statusChanged', true);
            
            // 
            if (actualStatus === 'error') {
              this.$set(this.errorReasons, server._id, '');
            } else if (actualStatus === 'online') {
              // 
              this.$delete(this.errorReasons, server._id);
              
              // 
              this.startHeartbeat(this.servers[index]);
            }
            
            // 2
            setTimeout(() => {
              this.$set(this.servers[index], 'statusChanged', false);
            }, 2000);
          }
          
          // 
          if (actualStatus === 'online') {
            this.$message.success(` ${server.name} `);
          } else if (actualStatus === 'error') {
            this.$message.error(` ${server.name} `);
          } else {
            this.$message.info(` ${server.name} : ${this.statusText[actualStatus]}`);
          }
        }
        
        // 
        this.saveStatesToCache();
        
        return actualStatus;
      } catch (error) {
        console.error(':', error);
        return 'error';
      } finally {
        this.$set(this.checkingServers, server._id, false);
      }
    },
    
    // 
    async checkServerLogs(server) {
      try {
        const logResponse = await this.getServerLogs(server._id);
        
        // 
        if (logResponse && logResponse.data) {
          const logs = logResponse.data;
          
          if (logs.includes('SSH') || 
              logs.includes('')) {
            // UI
            this.$alert(`
              <p>:</p>
              <p>: <strong></strong></p>
              <p>: <strong></strong></p>
              <p></p>
            `, '', {
              dangerouslyUseHTMLString: true,
              confirmButtonText: '',
              callback: () => {
                // 
                const index = this.servers.findIndex(s => s._id === server._id);
                if (index !== -1) {
                  this.$set(this.servers[index], 'status', 'online');
                  this.$set(this.servers[index], 'lastChecked', Date.now());
                  this.$delete(this.errorReasons, server._id);
                  
                  // 
                  this.startHeartbeat(this.servers[index]);
                  
                  this.$message.success('');
                  this.saveStatesToCache();
                }
              }
            });
          } else if (logs.includes('') || logs.includes('')) {
            // 
            this.$confirm(``, '', {
              confirmButtonText: '',
              cancelButtonText: '',
              type: 'warning'
            }).then(() => {
              this.handleConnect(server);
            }).catch(() => {});
          } else {
            // 
            this.$confirm(``, '', {
              confirmButtonText: '',
              cancelButtonText: '',
              type: 'info',
              closeOnClickModal: true
            }).then(() => {
              this.handleConnect(server);
            }).catch(() => {});
          }
        } else {
          // 
          this.$confirm(``, '', {
            confirmButtonText: '',
            cancelButtonText: '',
            type: 'info'
          }).then(() => {
            this.handleConnect(server);
          }).catch(() => {});
        }
      } catch (error) {
        console.error(':', error);
        this.$message.error(': ' + error.message);
      }
    },
    // 
    async autoFixInconsistentStatus() {
      console.log('...');
      
      // 
      const errorServers = this.servers.filter(s => s.status === 'error');
      for (const server of errorServers) {
        try {
          console.log(`: ${server.name}`);
          
          // 
          const logResponse = await this.getServerLogs(server._id);
          
          if (logResponse && logResponse.data) {
            const logs = logResponse.data;
            
            // 
            if (logs.includes('SSH') || 
                logs.includes('') ||
                logs.includes('')) {
              
              console.log(` ${server.name} `);
              
              // 
              const index = this.servers.findIndex(s => s._id === server._id);
              if (index !== -1) {
                this.$set(this.servers[index], 'status', 'online');
                this.$set(this.servers[index], 'lastChecked', Date.now());
                this.$delete(this.errorReasons, server._id);
                
                // 
                this.startHeartbeat(this.servers[index]);
                
                // 
                this.$message.success(` ${server.name} `);
              }
            }
          }
        } catch (error) {
          console.error(` ${server.name} :`, error);
        }
      }
      
      // 
      const offlineServers = this.servers.filter(s => s.status === 'offline');
      for (const server of offlineServers) {
        try {
          console.log(`: ${server.name}`);
          
          // 
          const statusResponse = await this.checkStatus(server._id);
          
          if (statusResponse && statusResponse.data && 
              (statusResponse.data.status === 'online' || 
               statusResponse.data.backendConnected)) {
            
            console.log(` ${server.name} `);
            
            // 
            const index = this.servers.findIndex(s => s._id === server._id);
            if (index !== -1) {
              this.$set(this.servers[index], 'status', 'online');
              this.$set(this.servers[index], 'lastChecked', Date.now());
              
              // 
              this.startHeartbeat(this.servers[index]);
              
              // 
              this.$message.success(` ${server.name} `);
            }
          }
        } catch (error) {
          console.error(` ${server.name} :`, error);
        }
      }
      
      // 
      this.saveStatesToCache();
    }
  }
};
</script>

