<template>
  <el-form 
    ref="passwordForm" 
    :model="passwordForm" 
    :rules="rules" 
    label-width="120px"
    @submit.native.prevent
  >
    <el-form-item label="" prop="currentPassword">
      <el-input 
        v-model="passwordForm.currentPassword" 
        type="password" 
        placeholder=""
      ></el-input>
    </el-form-item>
    <el-form-item label="" prop="newPassword">
      <el-input 
        v-model="passwordForm.newPassword" 
        type="password" 
        placeholder=""
      ></el-input>
    </el-form-item>
    <el-form-item label="" prop="confirmPassword">
      <el-input 
        v-model="passwordForm.confirmPassword" 
        type="password" 
        placeholder=""
        @keyup.enter.native="handleSubmit"
      ></el-input>
    </el-form-item>
    <el-form-item>
      <el-button type="primary" :loading="loading" @click="handleSubmit"></el-button>
      <el-button @click="resetForm"></el-button>
    </el-form-item>
  </el-form>
</template>

<script>
import axios from 'axios';

export default {
  name: 'ChangePasswordForm',
  data() {
    // 
    const validateConfirmPassword = (rule, value, callback) => {
      if (value !== this.passwordForm.newPassword) {
        callback(new Error(''));
      } else {
        callback();
      }
    };
    
    return {
      passwordForm: {
        currentPassword: '',
        newPassword: '',
        confirmPassword: ''
      },
      rules: {
        currentPassword: [
          { required: true, message: '', trigger: 'blur' }
        ],
        newPassword: [
          { required: true, message: '', trigger: 'blur' },
          { min: 6, message: '6', trigger: 'blur' }
        ],
        confirmPassword: [
          { required: true, message: '', trigger: 'blur' },
          { validator: validateConfirmPassword, trigger: 'blur' }
        ]
      },
      loading: false
    };
  },
  methods: {
    async handleSubmit() {
      try {
        // 
        await this.$refs.passwordForm.validate();
        
        this.loading = true;
        
        // 
        const response = await axios.put('/api/auth/update-password', {
          currentPassword: this.passwordForm.currentPassword,
          newPassword: this.passwordForm.newPassword
        });
        
        if (response.data.success) {
          this.$message.success('');
          this.resetForm();
          this.$emit('password-updated');
        }
      } catch (error) {
        if (error.response && error.response.data) {
          this.$message.error(error.response.data.message || '');
        } else if (!error.response) {
          // 
        } else {
          this.$message.error('');
        }
      } finally {
        this.loading = false;
      }
    },
    resetForm() {
      this.$refs.passwordForm.resetFields();
    }
  }
};
</script>

<style scoped>
.el-form {
  max-width: 500px;
}
</style> 