<template>
  <div class="server-form">
    <el-form :model="form" :rules="rules" ref="serverForm" label-width="100px">
      <el-form-item label="" prop="name">
        <el-input v-model="form.name" placeholder=""></el-input>
      </el-form-item>

      <el-form-item label="" prop="host">
        <el-input v-model="form.host" placeholder="IP"></el-input>
      </el-form-item>

      <el-form-item label="SSH" prop="port">
        <el-input-number v-model="form.port" :min="1" :max="65535" :step="1"></el-input-number>
      </el-form-item>

      <el-form-item label="" prop="username">
        <el-input v-model="form.username" placeholder=""></el-input>
      </el-form-item>

      <el-form-item label="" prop="authType">
        <el-radio-group v-model="form.authType">
          <el-radio label="password"></el-radio>
          <el-radio label="privateKey"></el-radio>
        </el-radio-group>
      </el-form-item>

      <el-form-item v-if="form.authType === 'password'" label="" prop="password">
        <el-input v-model="form.password" type="password" placeholder=""></el-input>
      </el-form-item>

      <el-form-item v-if="form.authType === 'privateKey'" label="" prop="privateKey">
        <el-input v-model="form.privateKey" type="textarea" :rows="8" placeholder=""></el-input>
      </el-form-item>

      <el-form-item>
        <el-button type="primary" @click="submitForm">{{ isEdit ? '' : '' }}</el-button>
        <el-button @click="resetForm"></el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
export default {
  name: 'ServerForm',
  props: {
    isEdit: {
      type: Boolean,
      default: false
    },
    serverData: {
      type: Object,
      default: () => ({})
    }
  },
  data() {
    return {
      form: {
        name: '',
        host: '',
        port: 22,
        username: '',
        authType: 'password',
        password: '',
        privateKey: ''
      },
      rules: {
        name: [
          { required: true, message: '', trigger: 'blur' },
          { min: 2, max: 50, message: ' 2  50 ', trigger: 'blur' }
        ],
        host: [
          { required: true, message: '', trigger: 'blur' }
        ],
        port: [
          { required: true, message: 'SSH', trigger: 'blur' },
          { type: 'number', message: '', trigger: 'blur' }
        ],
        username: [
          { required: true, message: '', trigger: 'blur' }
        ],
        password: [
          { required: true, message: '', trigger: 'blur' }
        ],
        privateKey: [
          { required: true, message: '', trigger: 'blur' }
        ]
      }
    }
  },
  created() {
    if (this.isEdit && this.serverData) {
      this.form = { ...this.form, ...this.serverData };
    }
  },
  methods: {
    submitForm() {
      this.$refs.serverForm.validate((valid) => {
        if (valid) {
          this.$emit('submit', this.form);
        } else {
          return false;
        }
      });
    },
    resetForm() {
      this.$refs.serverForm.resetFields();
    }
  }
}
</script>

<style scoped>
.server-form {
  max-width: 600px;
  margin: 0 auto;
}
</style> 